.reveal{
    position: relative;
    transform: translateY(150px);
    opacity: 0;
    transition: 1s all ease;
}

.reveal.active{
    transform: translateY(0);
    opacity: 1;
}
h1.three, h1.about-title{
    letter-spacing: 0;
    line-height: 1.5em;
    padding-bottom: 15px;
    position: relative;
}
h1.three:before, h1.about-title::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 5px;
    width: 55px;
    background-color: #14b8a6;
  }
h1.three:after, h1.about-title::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 2px;
    height: 1px;
    width: 95%;
    max-width: 255px;
    background-color: #99f6e4;
}
.button-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: opacity 0.3s ease-in;
    z-index: 1;
  }

  
  .reveal {
    position: relative;
  }
  
  .reveal:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 0.3s ease-in;
    z-index: 0;
  }
  
  .reveal:hover .button-container,
  .reveal:hover:before {
    opacity: 1;
  }
  
  

@media (max-width: 768px) {
    h1.three::before, h1.three::after{
        display: none;
    }
}